/* Brackets Fonts */

/* Alternative weights */

@font-weight-semibold: 500;
@font-weight-light: 200;


/* SourceSansPro Regular */
@font-face {
    font-family: 'SourceSansPro';
    src: url('fonts/SourceSansPro/SourceSansPro-Regular.ttf');
    font-weight: normal;
    font-style: normal;
}


/* SourceSansPro Light */
@font-face {
    font-family: 'SourceSansPro';
    src: url('fonts/SourceSansPro/SourceSansPro-Light.ttf');
    font-weight: @font-weight-light;
    font-style: normal;
}


/* SourceSansPro Semibold */
@font-face {
    font-family: 'SourceSansPro';
    src: url('fonts/SourceSansPro/SourceSansPro-Semibold.ttf');
    font-weight: @font-weight-semibold;
    font-style: normal;
}

/* SourceSansPro Italic */
@font-face {
    font-family: 'SourceSansPro';
    src: url('fonts/SourceSansPro/SourceSansPro-It.ttf');
    font-weight: normal;
    font-style: italic;
}


/* SourceCodePro Regular */
@font-face {
    font-family: 'SourceCodePro';
    src: url('fonts/SourceCodePro/SourceCodePro-Regular.ttf');
    font-weight: normal;
    font-style: normal;
}

/* SourceCodePro Medium */
@font-face {
    font-family: 'SourceCodePro-Medium';
    src: url('fonts/SourceCodePro/SourceCodePro-Medium.ttf');
    font-weight: normal;
    font-style: normal;
}

/* Font Stacks */

@sansFontFamily: 'SourceSansPro', Helvetica, Arial, "Meiryo UI", " ", "MS PGothic", sans-serif;

@sourceFontFamily: "SourceCodePro", "Menlo Regular", Consolas, Inconsolata, "Vera Sans", "Lucida Console", Courier, fixed;
@sourceFontFamily-Medium: "SourceCodePro-Medium", " ", "MS Gothic", monospace;
